import React, { useContext, useState, useEffect } from "react";
import { Button } from "primereact/button";
import { FormattedMessage } from "react-intl";
import { langContext } from "../../../context/langContext";
import { Sidebar } from "primereact/sidebar";
import { Dialog } from "primereact/dialog";

const Header = () => {
  const [actualState, changeCheckState] = useState(true);
  const idioma = useContext(langContext);
  const [visibleLeft, setVisibleLeft] = useState(false);
  const [position, setPosition] = useState("center");
  const [displayResponsive, setDisplayResponsive] = useState(false);

  const dialogFuncMap = {
    displayResponsive: setDisplayResponsive,
  };

  const onClick = (name, position) => {
    dialogFuncMap[`${name}`](true);

    if (position) {
      setPosition(position);
    }
  };

  const onHide = (name) => {
    dialogFuncMap[`${name}`](false);
  };

  const renderFooter = (name) => {
    return (
      <div>
        <Button
          label="No"
          icon="pi pi-times"
          onClick={() => onHide(name)}
          className="p-button-text"
        />
        <Button
          label="Yes"
          icon="pi pi-check"
          onClick={() => onHide(name)}
          autoFocus
        />
      </div>
    );
  };

  function useScrollDirection() {
    const [scrollDirection, setScrollDirection] = useState(null);

    useEffect(() => {
      let lastScrollY = window.pageYOffset;

      const updateScrollDirection = () => {
        const scrollY = window.pageYOffset;
        const direction = scrollY > lastScrollY ? "down" : "up";
        if (
          direction !== scrollDirection &&
          (scrollY - lastScrollY > 0 || scrollY - lastScrollY < -1)
        ) {
          setScrollDirection(direction);
        }
        lastScrollY = scrollY > 0 ? scrollY : 0;
      };
      window.addEventListener("scroll", updateScrollDirection); // add event listener
      return () => {
        window.removeEventListener("scroll", updateScrollDirection); // clean up
      };
    }, [scrollDirection]);

    return scrollDirection;
  }

  const lang = localStorage.getItem("lang");

  const handleCheckbox = (e) => {
    changeCheckState(e.target.checked);
    idioma.establecerLenguaje(actualState);
  };

  useEffect(() => {
    if (lang) {
      if (lang === "es-ES") {
        changeCheckState(false);
      } else if (lang === "en-US") {
        changeCheckState(true);
      } else {
        changeCheckState(true);
      }
    }
  }, []);

  const scrollDirection = useScrollDirection();
  return (
    <header
      className={`${
        scrollDirection === "down"
          ? "hidden"
          : "block headerFix topSectionHeader"
      }  z-5`}
    >
      <div className="z-1 flex flex-row pHorizontalUniversity  boxHeader">
        <div className="col-10 md:col-2 lg:col-6 font-bold text-white flex align-items-center boxLogo lg:justify-content-start">
          <a href="./">
            <img
              className="objectFit boxLogoLab "
              src={require("../../../assets/img/u/logoUni.webp")}
              alt=""
            />
          </a>
        </div>
        <div className="col-2 md:col-10 lg:col-6 font-bold text-white flex justify-content-end md:justify-content-end md:justify-content-around align-items-center nav lg:px-3 textHeader">
          <a
            href="./"
            className="font-semibold cursor-pointer hidden md:block mx-2 capitalize"
          >
            <FormattedMessage id="university.title" defaultMessage="labs" />
          </a>
          <a
            href="#company"
            className="font-semibold cursor-pointer hidden md:block mx-2"
          >
            <FormattedMessage
              id="header.community"
              defaultMessage="Community"
            />
          </a>

          <button
            className="button-85 hidden md:block ml-2 "
            onClick={() => onClick("displayResponsive")}
          >
            <div className="flex flex-row">
              <div>
                <img
                  className="objectFit boxWalletImg"
                  src={require("../../../assets/img/u/WALLET.webp")}
                  alt=""
                />
              </div>

              <div className="font-semibold">
                <FormattedMessage
                  id="header.connect"
                  defaultMessage="Connect Wallet"
                />
              </div>
            </div>
          </button>

          <Dialog
            visible={displayResponsive}
            onHide={() => onHide("displayResponsive")}
            breakpoints={{ "960px": "75vw" }}
            style={{ width: "50vw" }}
          >
            <div className="font-bold text-center">
              Conecta nuestra billetera en Solana para continuar
            </div>
            <div className="flex w-full justify-content-center mt-7">
              <div className="boxWallet cursor-pointer">
                <img
                  className="objectFit w-full p-2"
                  src={require("../../../assets/img/u/genesysLogo.webp")}
                  alt=""
                />
              </div>
            </div>
          </Dialog>

          <div class="checkbox-con">
            <input
              class="cursor-pointer"
              id="checkbox"
              checked={actualState}
              onChange={handleCheckbox}
              type="checkbox"
            />
          </div>

          <div className="block md:hidden">
            <Sidebar
              className="block md:hidden"
              visible={visibleLeft}
              onHide={() => setVisibleLeft(false)}
            >
              <div className="flex flex-column btnHeader">
                <a
                  data-aos="fade-up"
                  data-aos-delay="200"
                  href="#company"
                  className="btnHeader font-light cursor-pointer mx-2"
                >
                  Labs
                </a>
                <a
                  data-aos="fade-up"
                  data-aos-delay="400"
                  href="#services"
                  className="btnHeader font-light cursor-pointer mx-2"
                >
                  <FormattedMessage
                    id="header.community"
                    defaultMessage="Community"
                  />
                </a>
                <button
                  className="button-85 ml-2 mt-2 absolute boxbtnConnect"
                  data-aos="fade-up"
                  data-aos-delay="500"
                  onClick={() => onClick("displayResponsive")}
                >
                  <div className="flex flex-row">
                    <div>
                      <img
                        className="objectFit boxWalletImg"
                        src={require("../../../assets/img/u/WALLET.webp")}
                        alt=""
                      />
                    </div>

                    <div className="font-semibold">
                      <FormattedMessage
                        id="header.connect"
                        defaultMessage="Connect Wallet"
                      />
                    </div>
                  </div>
                </button>
              </div>
            </Sidebar>
            <Button
              icon="pi pi-bars"
              onClick={() => setVisibleLeft(true)}
              className="mr-2"
            />
          </div>
        </div>
      </div>

      {/* <div className="borderGradient"></div> */}
    </header>
  );
};

export default Header;
