import "./css/styles/styles.css";
import "primereact/resources/themes/arya-blue/theme.css"; //theme
import "primereact/resources/primereact.min.css"; //core css
import "primeicons/primeicons.css"; //icons
import "primeflex/primeflex.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import React from "react";
import IndexWeb from "./components/web";
import IndexU from "./components/university";

function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" exact element={<IndexWeb />} />
        <Route path="/university" element={<IndexU />} />
        <Route path="*" element={<IndexWeb />} />

      </Routes>
    </BrowserRouter>
  );
}

export default App;
