import React from 'react'

const Footer = () => {
  return (
    <footer className='pHorizontal pHorizontalMovil bg-black-alpha-90  flex justify-content-center align-items-center flex-column border-top-1 border-white' name="contact" id="contact">
      <div className='col-12 grid '>
          <a href="https://www.linkedin.com/company/xolarixlab/" target="_blank" rel="noopener noreferrer" className='col-12 sm:col-6 md:col-4 flex cursor-pointer boxFooter boxFooterLink justify-content-center'>
            <div>
              <img
                className="objectFit z-0 imgFooter"
                src={require("../../../assets/img/linkedin.png")}
                alt=""
              />
            </div>
            
            <div className='flex align-items-center font-medium text-white boxDescriptionFooter'>
              Xolarix
            </div>
          </a>
          <a href="https://github.com/Xolarix-lab" target="_blank"  rel="noopener noreferrer" className='col-12 sm:col-6 md:col-4 flex cursor-pointer boxFooter justify-content-center'>
            <div>
              <img
                className="objectFit z-0 imgFooter_git"
                src={require("../../../assets/img/github.png")}
                alt=""
              />
            </div>
            
            <div className='flex align-items-center font-medium text-white boxDescriptionFooter'>
              GitHub
            </div>
          </a>
          <a href="https://mobile.twitter.com/Xolarixlab" target="_blank"  rel="noopener noreferrer" className='col-12 sm:mt-2 md:mt-0 sm:col-12 md:col-4 flex cursor-pointer boxFooter justify-content-center'>
            <div>
              <img
                className="objectFit z-0 imgFooter"
                src={require("../../../assets/img/twitter.png")}
                alt=""
              />
            </div>
            
            <div className='flex align-items-center font-medium text-white boxDescriptionFooter'>
              Twitter
            </div>
          </a>
      </div>
      <a href="mailto:contactus@xolarix.io" className='col-12 boxGradientThreeBlackTitle mediumSpringGreenOne boxMail font-medium cursor-pointer boxFooter'>
        contactus@xolarix.io
      </a >
    </footer>
    
  )
}

export default Footer