import React, { useContext, useState, useEffect, useRef } from "react";
import { Button } from "primereact/button";
import { FormattedMessage } from "react-intl";
import { langContext } from "../../../context/langContext";
import { Sidebar } from "primereact/sidebar";

import { Menu } from "primereact/menu";

const Header = () => {
  const [actualState, changeCheckState] = useState(true);
  const idioma = useContext(langContext);
  const [visibleLeft, setVisibleLeft] = useState(false);

  function useScrollDirection() {
    const [scrollDirection, setScrollDirection] = useState(null);

    useEffect(() => {
      let lastScrollY = window.pageYOffset;

      const updateScrollDirection = () => {
        const scrollY = window.pageYOffset;
        const direction = scrollY > lastScrollY ? "down" : "up";
        if (
          direction !== scrollDirection &&
          (scrollY - lastScrollY > 0 || scrollY - lastScrollY < -1)
        ) {
          setScrollDirection(direction);
        }
        lastScrollY = scrollY > 0 ? scrollY : 0;
      };
      window.addEventListener("scroll", updateScrollDirection); // add event listener
      return () => {
        window.removeEventListener("scroll", updateScrollDirection); // clean up
      };
    }, [scrollDirection]);

    return scrollDirection;
  }
  const scrollDirection = useScrollDirection();

  const lang = localStorage.getItem("lang");

  const handleCheckbox = (e) => {
    changeCheckState(e.target.checked);
    idioma.establecerLenguaje(actualState);
  };

  useEffect(() => {
    if (lang) {
      if (lang === "es-ES") {
        changeCheckState(false);
      } else if (lang === "en-US") {
        changeCheckState(true);
      } else {
        changeCheckState(true);
      }
    }
  }, []);

  const devs = useRef(null);
  const preojects = useRef(null);


  const itemsDev = [
    {
      label: "Xolarix Lab",
      command: (event) => {
        window.location = "/university";
        event.preventDefault();
      },
    },
    {
      label: (<FormattedMessage id="header.recruit" defaultMessage="Recruitment" />),
      command: (event) => {
        window.location = "/";
        event.preventDefault();
      },
    },
  ];


  const itemsWallets = [
    {
      label: (<FormattedMessage id="header.wallet" defaultMessage="Genesys Wallet" />),
      command: (event) => {
        window.location = "/";
        event.preventDefault();
      },
    },
    {
      label: "Genesys Fintech",
      command: (event) => {
        window.location = "/";
        event.preventDefault();
      },
    },
  ];




  

  // const dropdowns = document.querySelectorAll(".dropdown");

  // dropdowns.forEach((dropdown) => {
  //   const select = dropdown.querySelector(".select");
  //   const caret = dropdown.querySelector(".caret");
  //   const menu = dropdown.querySelector(".menu");
  //   const options = dropdown.querySelector(".menu li");
  //   const selected = dropdown.querySelector(".selected");

  //   select.addEventListener("click", () => {
  //     select.classList.toggle("select-clicked");
  //     caret.classList.toggle("caret-rotate");
  //     menu.classList.toggle("menu-open");
  //   });

  //   options.forEach((option) => {
  //     option.addEventListener("click", () => {
  //       selected.innertText = option.innertText;
  //       select.classList.remove("select-clicked");
  //       caret.classList.remove("caret-rotate");
  //       menu.classList.remove("menu-open");
  //       options.forEach((option) => {
  //         option.classList.remove("active");
  //       });
  //       option.classList.add("active");
  //     });
  //   });
  // });

  return (
    <header
      className={`${
        scrollDirection === "down"
          ? "hidden"
          : "block headerFix topSectionHeader"
      }  z-5`}
    >
      <div className="z-1 flex flex-row pHorizontal  boxHeader">
        <div className="col-10 md:col-2 lg:col-3 font-bold text-white flex align-items-center boxLogo lg:justify-content-start">
          <a href="./">
            <img
              className="objectFit boxLogoImg "
              src={require("../../../assets/img/logoHeader.png")}
              alt=""
            />
          </a>
        </div>
        <div className="col-2 md:col-10 lg:col-9 font-bold text-white flex justify-content-end md:justify-content-end md:justify-content-around align-items-center nav lg:px-3 textHeader">
          <a
            href="#company"
            className="font-light cursor-pointer hidden md:block mx-2"
          >
            <FormattedMessage id="menu.company" defaultMessage="Our Company" />
          </a>
          <a
            href="#services"
            className="font-light cursor-pointer hidden md:block mx-2"
          >
            <FormattedMessage
              id="menu.services"
              defaultMessage="Our Services"
            />
          </a>
          <a
            href="#contact"
            className="font-light cursor-pointer hidden md:block mx-2"
          >
            <FormattedMessage id="menu.contact" defaultMessage="Contact" />
          </a>
          <a
            href="#technologies"
            className="font-light cursor-pointer hidden md:block mx-2"
          >
            <FormattedMessage
              id="menu.technologies"
              defaultMessage="Technologies"
            />
          </a>

          <Menu model={itemsDev} popup ref={devs} id="popup_menu" />
          <Button
            label={<FormattedMessage id="header.dev" defaultMessage="Developers" />}
            icon="pi pi-angle-down flex-order-1 mr-0 ml-1 "
            onClick={(event) => devs.current.toggle(event)}
            aria-controls="popup_menu"
            aria-haspopup
            className="font-light textHeader"
          />

          <Menu model={itemsWallets} popup ref={preojects} id="popup_menu" />
          <Button
            label={<FormattedMessage id="header.projects" defaultMessage="Our Projects" />}
            icon="pi pi-angle-down flex-order-1 mr-0 ml-1 "
            onClick={(event) => preojects.current.toggle(event)}
            aria-controls="popup_menu"
            aria-haspopup
            className="font-light textHeader"
          />

          {/* <div className="dropdown">
            <div className="select">
              <span className="selected">Desarrolladores</span>
              <div className="caret"></div>
            </div>
            <ul className="menu">
              <li className="active">Facebook</li>
              <li>Xolarix Lab</li>
              <li>Reclutamiento</li>
            </ul>
          </div> */}

          {/* <button className="button-85 hidden md:block ml-2">
            <FormattedMessage id="menu.launchApp" defaultMessage="Launch App" />
          </button>  */}

          <div class="checkbox-con">
            <input
              class="cursor-pointer"
              id="checkbox"
              checked={actualState}
              onChange={handleCheckbox}
              type="checkbox"
            />
          </div>

          <div className="block md:hidden">
            <Sidebar
              className="block md:hidden"
              visible={visibleLeft}
              onHide={() => setVisibleLeft(false)}
            >
              <div className="flex flex-column btnHeader">
                <a
                  data-aos="fade-up"
                  data-aos-delay="200"
                  href="#company"
                  className="btnHeader font-light cursor-pointer mx-2"
                >
                  <FormattedMessage
                    id="menu.company"
                    defaultMessage="Our Company"
                  />
                </a>
                <a
                  data-aos="fade-up"
                  data-aos-delay="400"
                  href="#services"
                  className="btnHeader font-light cursor-pointer mx-2"
                >
                  <FormattedMessage
                    id="menu.services"
                    defaultMessage="Our Services"
                  />
                </a>
                <a
                  data-aos="fade-up"
                  data-aos-delay="600"
                  href="#technologies"
                  className="btnHeader font-light cursor-pointer mx-2"
                >
                  <FormattedMessage
                    id="menu.technologies"
                    defaultMessage="Technologies"
                  />
                </a>
                <a
                  data-aos="fade-up"
                  data-aos-delay="800"
                  href="#contact"
                  className="btnHeader font-light cursor-pointer mx-2"
                >
                  <FormattedMessage
                    id="menu.contact"
                    defaultMessage="Contact"
                  />
                </a>
              </div>
            </Sidebar>
            <Button
              icon="pi pi-bars"
              onClick={() => setVisibleLeft(true)}
              className="mr-2"
            />
          </div>
        </div>
      </div>
      <section class="seperator-wrapper">
        <div class="seperator gradient"></div>
      </section>
      {/* <div className="borderGradient"></div> */}
    </header>
  );
};

export default Header;
